@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  scroll-behavior: smooth;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #011c40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #f9fbfd;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1250px;
  margin: 0 auto;
  padding: 0 45px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.header {
  position: absolute;
  z-index: 10;
  top: 0;
  right: 0;
  left: 0;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 100px;
  padding-top: 30px;
  padding-bottom: 30px;
}
@media (max-width: 1023px) {
  .header__container {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding-top: 11px;
    padding-right: 0;
    padding-bottom: 11px;
  }
}

.header__logo-link {
  display: flex;
}
@media (max-width: 767px) {
  .header__logo-link {
    position: relative;
    z-index: 20;
  }
}
.header__logo-link:hover {
  opacity: 0.6;
}
.header__logo-link:focus-visible {
  outline: 2px solid #f9fbfd;
  outline-offset: 4px;
}
.header__logo-link:active {
  opacity: 0.3;
}

@media (max-width: 767px) {
  .header.is-open .header__logo-link {
    transform: translateX(36px);
  }
  .header.is-open .header__logo-link:focus-visible {
    outline-color: #011c40;
  }
}

.header__logo {
  --tabletLogo: none;
  --mobileLogo: none;
  width: 70px;
  height: 38px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__logo {
    --desktopLogo: none;
    --tabletLogo: block;
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    --tabletLogo: none;
    --mobileLogo: block;
    width: 42px;
    height: 22px;
  }
}

@media (max-width: 767px) {
  .header.is-open .header__logo {
    color: #011c40;
  }
}

@media (max-width: 767px) {
  .header__list-overlay {
    position: fixed;
    z-index: 15;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    background-color: rgba(0, 43, 102, 0.5);
    cursor: pointer;
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
  }
}

@media (max-width: 767px) {
  .header.is-open .header__list-overlay {
    opacity: 1;
    pointer-events: all;
    visibility: visible;
  }
}

@media (max-width: 767px) {
  .header__list-wrapper {
    position: fixed;
    z-index: 15;
    top: 0;
    right: 0;
    bottom: 0;
    left: 36px;
    padding: 75px 30px 45px 15px;
    background-color: #f9fbfd;
    transform: translateX(100%);
    cursor: default;
  }
}

@media (max-width: 767px) {
  .header.is-open .header__list-wrapper {
    transform: translateX(0);
  }
}

.header__list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px 33px;
  margin: 0;
  margin-top: 5px;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .header__list {
    gap: 10px 24px;
    margin-top: 2px;
  }
}
@media (max-width: 767px) {
  .header__list {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    gap: 10px;
    max-height: 100%;
    margin-top: 0;
    overflow-y: auto;
  }
}

.header__link {
  display: inline-block;
  padding: 10px 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .header__link {
    padding: 10px 10px 8px;
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
}
.header__link:hover {
  opacity: 0.6;
}
.header__link:focus-visible {
  outline: 2px solid #f9fbfd;
  outline-offset: 4px;
}
@media (max-width: 767px) {
  .header__link:focus-visible {
    outline-color: #011c40;
    outline-offset: -2px;
  }
}
.header__link:active {
  opacity: 0.3;
}

.header__toggle {
  position: relative;
  display: none;
  width: 40px;
  height: 40px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.header__toggle::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 24px;
  height: 2px;
  background-color: #f9fbfd;
  transform: translate(-50%, -50%);
  box-shadow: 0 6px 0 0 #f9fbfd, 0 -6px 0 0 #f9fbfd;
}
@media (max-width: 767px) {
  .header__toggle {
    display: block;
    margin-right: 7px;
  }
}
.header__toggle:hover {
  opacity: 0.6;
}
.header__toggle:focus-visible {
  outline: 2px solid #f9fbfd;
  outline-offset: 4px;
}
.header__toggle:active {
  opacity: 0.3;
}

@media (max-width: 767px) {
  .header.is-open .header__toggle:focus-visible {
    outline-color: #011c40;
    outline-offset: -2px;
  }
}
@media (max-width: 767px) {
  .header.is-open .header__toggle {
    z-index: 20;
    margin-right: 0;
  }
  .header.is-open .header__toggle::before {
    width: 18px;
    height: 2px;
    background-color: #011c40;
    box-shadow: none;
    transform: translate(-50%, -50%) rotate(45deg);
  }
  .header.is-open .header__toggle::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 18px;
    height: 2px;
    background-color: #011c40;
    transform: translate(-50%, -50%) rotate(-45deg);
  }
}

.footer {
  background-color: #011c40;
}

.footer__container {
  display: flex;
  gap: 40px;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px;
}
@media (max-width: 1023px) {
  .footer__container {
    gap: 25px;
    padding-top: 42px;
    padding-bottom: 43px;
  }
}
@media (max-width: 767px) {
  .footer__container {
    flex-direction: column;
    align-items: flex-start;
    gap: 18px;
    padding-top: 41px;
    padding-bottom: 35px;
  }
}

.footer__logo-link {
  display: flex;
}
.footer__logo-link:hover {
  opacity: 0.6;
}
.footer__logo-link:focus-visible {
  outline: 2px solid #f9fbfd;
  outline-offset: 4px;
}
.footer__logo-link:active {
  opacity: 0.3;
}

.footer__logo {
  --tabletLogo: none;
  --mobileLogo: none;
  width: 70px;
