  }
}

.footer__link-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px 33px;
  max-width: 65%;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .footer__link-list {
    gap: 10px 23px;
    max-width: 70%;
  }
}
@media (max-width: 767px) {
  .footer__link-list {
    flex-direction: column;
    gap: 5px;
    max-width: unset;
  }
}

.footer__link {
  display: inline-block;
  padding: 10px 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .footer__link {
    font-size: 12px;
    line-height: 15px;
  }
}
@media (max-width: 767px) {
  .footer__link {
    padding: 5px 0;
  }
}
.footer__link:hover {
  opacity: 0.6;
}
.footer__link:focus-visible {
  outline: 2px solid #f9fbfd;
  outline-offset: 4px;
}
.footer__link:active {
  opacity: 0.3;
}

.footer__social-list {
  display: flex;
  flex-wrap: wrap;
  gap: 18px;
  max-width: 30%;
  margin: 0;
  margin-right: -5px;
  margin-left: auto;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .footer__social-list {
    gap: 12px;
